import loadScript from 'load-script'

let initialized = false;
let queue = [];
const wd = window;
export const FBService = (callback) => {
    if (initialized) {

        callback(wd.FB)
    }
    else {
        queue.push(callback);
        if (!wd.fbAsyncInit) {
            wd.fbAsyncInit = () => {
                wd.FB.init({
                    appId: "108195924915256",
                    autoLogAppEvents: true,
                    status: true,
                    cookie: true,
                    xfbml: false,
                    version: 'v3.2',
                })
            }
            initialized = true;
        }
        queue.forEach(cb => cb(wd.FB));
        queue = null;
    }
    const script = window.localStorage.getItem('fb:debug') === 'true'
        ? 'xfbml.customerchat/debug.js'
        : 'xfbml.customerchat.js';
    loadScript(`https://connect.facebook.net/en_US/sdk/${script}`, { async: true });
}