import React from "react";
import { useEffect } from "react";
import { FBService } from '../services/fbmessagerservices'
export const CustomerChat: React.FC = () => {
    useEffect(() => {
        const time_out = setTimeout(() => {
            FBService((fb: any) => {
                time_out && fb?.XFBML?.parse()
            })
        }, 2000)
        return () => {
            clearTimeout(time_out)
        }
    }, [])
    const configContext = React.createContext({})
    return (
        <configContext.Consumer>
            {
                (config: any) => (
                    <div {... { className: 'fb-customerchat', attribution: "setup_tool", page_id: config?.facebook?.pageId }}>

                    </div>
                )
            }
        </configContext.Consumer>
    )
}